
$dib: inline-block;
header{
    color: #fff;
    font-size: 100%;
    box-shadow: 0.001em 0 0.1em black;
    .row{
        display: flex;
        justify-content: space-around;
        .col{
            a{
                text-decoration: none;
                color: #333;
                padding: .8em 1.5em;
                display: $dib;
                margin: .5em;
                border-radius: 1em;
                box-shadow: 0 0.01em .05em black;
            }
        }
    }
}